export function extractFields(data, options) {
  if (!data) {
    return data
  }
  if (data instanceof Array) {
    return data.map(item => {
      const obj = extractFields(item, options)
      if (item.children && item.children.length > 0) {
        obj.children = extractFields(item.children, options)
        return obj
      } else {
        return obj
      }
    })
  } else {
    const keys = Object.keys(options.alias)
    const obj = {}
    keys.forEach(key => {
      obj[key] = data[options.alias[key]]
    })
    return obj
  }
}

/**
 * 
 * @param {*} array 
 * 
 * @returns array
 * 
 * @example
 *  array: [['', ''], ['16G', '32G']]
 *  return: ['|16G', '|32G', '|16G', '|32G']
 */
export function combineAll(array) {
  let result = []
  const length = array.length
  if (length >= 2) {
    result = combine(array[0], array[1])
    let index = 2
    while (index < length) {
      result = combine(result, array[index])
      index++
    }
    return result
  } else {
    return array
  }
}

/**
 * 
 * @param {*} array1 
 * @param {*} array2 
 * 
 * @returns array
 * 
 * @example
 *  array1: ['', '']
 *  array2: ['16G', '32G']
 *  return: ['|16G', '|32G', '|16G', '|32G']
 */
export function combine(array1, array2) {
  const result = []
  array1.forEach(item1 => {
    array2.forEach(item2 => {
      result.push(`${item1}|${item2}`)
    })
  })
  return result
}

/**
 * 
 * @param {*} img 
 * @param {*} callback   Base64 url
 * 
 * @example
 *  getBase64(info.file.originFileObj, imageUrl => {
 *    this.imageUrl = imageUrl;
 *    this.loading = false;
 *  });
 */
export function getBase64(img, callback) {
  const reader = new FileReader();
  reader.addEventListener('load', () => callback(reader.result));
  reader.readAsDataURL(img);
}

export function setStoreItemByBase64(key, value) {
  localStorage.setItem(key, btoa(encodeURI(JSON.stringify(value))))
}

export function getStoreItemByBase64(key) {
  return JSON.parse(decodeURI(atob(localStorage.getItem(key))))
}